
.my-background {
  background: url('/static/images/body-background.picture') no-repeat fixed;
  background-position: 50% -50px;
}
.blur {
  position: relative;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0);
}
.blur:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  content: '';
  -webkit-filter: blur(5px);
          filter: blur(5px);
  background: url('/static/images/body-background.picture') no-repeat fixed;
  background-position: 50% -50px;
}
.container {
  width: 800px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 700px) {
.container {
    width: 100%;
}
}
@media (max-width: 700px) {
.top-left-padding {
    padding-left: 1em;
}
}



